* {
  -webkit-user-select: none;
}
input {
  -webkit-user-select: text;
}
body .loading-mask {
  position: fixed;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.65) url(./45297c31ffa896365096625c6a352659.svg) no-repeat center center;
  background-size: 1rem auto;
  z-index: 400;
  left: 0;
  top: 0;
  color: #fff;
  font-size: 0.16rem;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}
body .loading-mask .loading-text {
  margin-top: 1rem;
  -webkit-animation: blink 2s 0s linear infinite;
  -webkit-animation-fill-mode: forwards;
  -moz-animation: blink 2s 0s linear infinite;
  -ms-animation: blink 2s 0s linear infinite;
}
body .loading-mask .loading-close {
  width: 0.3rem;
  height: 0.3rem;
  background: rgba(255, 255, 255, 0.6) url(./b81ec460fa214c7304087c8c384fa698.png) no-repeat center center;
  background-size: 0.15rem auto;
  position: absolute;
  right: 0.15rem;
  top: 0.15rem;
  border-radius: 50%;
}
body .mask {
  left: 0;
  top: 0;
  position: fixed;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.65);
  z-index: 300;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  transition: opacity 200ms 0s linear;
}
body .mask.dialog-layer .dialog {
  display: block;
  border: 0;
  width: 4.38rem;
  background: #fff;
  border-radius: 0.1rem;
  box-shadow: 0px 2px 12px 0px rgba(0, 0, 0, 0.29);
  overflow: hidden;
  height: auto;
  transform: scale(0);
  transition: all 200ms 0s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}
body .mask.dialog-layer .dialog .title {
  height: 0.4rem;
  background: #eeeeee;
  font-size: 0.2rem;
  color: #000232;
  line-height: 0.4rem;
  padding-left: 0.17rem;
  position: relative;
}
body .mask.dialog-layer .dialog .title .close-btn {
  width: 0.4rem;
  height: 0.4rem;
  background: url(./d84b44ad4f6a89fb4b94c334b69646af.png) no-repeat center center;
  background-size: 0.16rem auto;
  position: absolute;
  right: 0;
  top: 0;
}
body .mask.dialog-layer .dialog .content {
  display: block;
  margin: 0;
  padding-bottom: 0.3rem;
  height: auto;
}
body .mask.dialog-layer .dialog .content.nobtn {
  padding-bottom: 0;
}
body .mask.dialog-layer .dialog .content .texts {
  line-height: 2;
  font-size: 0.18rem;
  color: #333333;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.4rem;
}
body .mask.dialog-layer .dialog .content .btns {
  display: flex;
  justify-content: space-around;
}
body .mask.dialog-layer .dialog .content .btns button {
  width: 1.24rem;
  height: 0.38rem;
  font-size: 0.2rem;
  border-radius: 0.03rem;
  border: 1px solid #2f80c5;
}
body .mask.dialog-layer .dialog .content .btns button.ok-btn {
  background: #2f80c5;
  color: #fff;
}
body .mask.dialog-layer .dialog .content .btns button.cancel-btn {
  color: #2f80c5;
  background: #fff;
}
body .mask.show {
  opacity: 1;
}
body .mask.show .dialog {
  transform: scale(1);
}
body .calendar-box {
  font-size: 0.17rem;
  color: #000000;
  width: 8.2rem;
  margin: 0 auto 0.3rem;
  background: #fff;
}
body .cal-bd {
  border-bottom: 0.01rem solid #cccccc;
}
body .highlight {
  color: #ff2a2a;
}
body .calendar-head {
  height: 0.46rem;
  display: flex;
  align-items: center;
}
body .calendar-head span {
  flex: 1;
  text-align: center;
}
body .calendar-control {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 0.5rem;
  color: #000;
}
body .calendar-control .control-l,
body .calendar-control .control-r {
  width: 0.6rem;
  height: 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
body .calendar-control .control-l img,
body .calendar-control .control-r img {
  width: 0.07rem;
}
body .calendar-control .control-r img {
  transform: scaleX(-1);
}
body .calendar-control .control-c {
  text-align: center;
  margin: 0 0.3rem;
}
body .calendar-row {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 0.46rem;
}
body .calendar-col {
  width: 0.33rem;
  height: 0.33rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  position: relative;
}
body .calendar-col.disabled {
  color: #fff;
  background: #cacaca;
}
body .calendar-col.selected {
  color: #fff;
  background: #ff2a2a;
}
body .calendar-col.highlighted::after {
  content: "";
  position: absolute;
  width: 0.06rem;
  height: 0.06rem;
  border-radius: 50%;
  background: #ff2a2a;
  bottom: 0;
  left: 50%;
  margin-left: -0.03rem;
}
html,
body {
  height: 100%;
}
html,
body {
  background: #484f70;
  color: #fff;
}
html #app,
body #app,
html .page,
body .page {
  width: 100%;
  height: 100%;
}
html .page,
body .page {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
html .close-btn,
body .close-btn {
  width: 29px;
  height: 29px;
  background: url(./44505af3475f7a9c47dfdedf01f1c5bb.png) no-repeat center center;
  background-size: 100% auto;
  position: absolute;
  right: 5px;
  top: 5px;
}
html .logo,
body .logo {
  width: 72px;
  height: 72px;
  background: url(./796607b3631731a9833d0b91fb48cce9.png) no-repeat center center;
  background-size: 100% auto;
  margin-bottom: 22px;
}
html .tips,
body .tips {
  font-size: 18px;
  color: #ffffff;
  max-width: 400px;
  text-align: center;
  line-height: 1.5;
}
html .progress,
body .progress {
  margin-top: 20px;
  width: 315px;
  height: 20px;
  display: flex;
  align-items: center;
}
html .progress .bar,
body .progress .bar {
  margin-right: 10px;
  width: 270px;
  height: 14px;
  border-radius: 14px;
  background: #fff;
}
html .progress .bar .bar-i,
body .progress .bar .bar-i {
  border-radius: 14px;
  background: #44a650;
  height: 100%;
}
html .progress .txt,
body .progress .txt {
  font-size: 14px;
}
html .restart-btn,
body .restart-btn {
  width: 80px;
  height: 32px;
  font-size: 14px;
  border-radius: 5px;
  color: #333;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #fff;
  cursor: pointer;
  margin-top: 20px;
}
html .restart-btn:hover,
body .restart-btn:hover {
  background: rgba(255, 255, 255, 0.8);
}
